.App {
  height: 100vh;
  width: 100vw;
  background-image: linear-gradient(0deg, transparent 24%, #eee 25%, #eee 26%, transparent 27%, transparent 74%, #eee 75%, #eee 76%, transparent 77%, transparent),
    linear-gradient(90deg, transparent 24%, #eee 25%, #eee 26%, transparent 27%, transparent 74%, #eee 75%, #eee 76%, transparent 77%, transparent);
  background-size: 50px 50px;
  position: relative;
  overflow: hidden;
}

.board {
  width: 100%;
  height: 100%;
  position: relative;
}
