.note {
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 220px;
    padding: 8px;
    box-shadow: 2px 2px 8px rgba(0,0,0,0.1);
    user-select: none;
    cursor: grab;
  }
  
  .note textarea {
    width: 100%;
    height: 100px;
    resize: none;
    background: transparent;
    border: none;
    outline: none;
    font-family: sans-serif;
    font-size: 14px;
  }
  
  .note-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
  }
  
  .note canvas {
    border: 1px solid #aaa;
    background: #fff;
    display: block;
  }
  
  .note-body input[type=\"file\"] {
    width: 100%;
  }
  